@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#737373}.container{max-width:1280px;margin:0 auto}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality-gh-page/static/media/hero-bg.5d86912d.jpg);background-size:cover;height:900px;padding:80px 0 178px}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:45px}.hero-navigation-item{text-decoration:none;font-weight:normal;font-size:19px;line-height:22px;color:#fff;margin-left:74px}.hero-navigation-item:first-child{margin-left:0}.hero-title{width:664px;font-weight:normal;font-size:57px;line-height:67px;color:#fff;margin:108px 0 132px}.hero-link{display:inline-block;padding:35px 151px;text-decoration:none;font-weight:normal;font-size:32px;line-height:37px;color:#fff;background:#B78A3F}

.facts{box-sizing:border-box;position:relative;padding:100px 60px 50px}.facts-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#fff;margin-bottom:30px}.facts-slide{justify-content:center;width:auto}.facts-slide-desc{margin-top:40px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#fff}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#B78A3F}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{opacity:0}.facts-pagination{display:flex;justify-content:center;margin-top:66px}.facts-pagination-bullet{display:inline-block;width:16px;height:16px;border-radius:100%;margin:0 20px;background-color:transparent;border:2px solid #B78A3F;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}

.sectors{padding:50px 0}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center;margin-left:20px;margin-right:20px}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#FFFFFF;margin:42px 20px 30px}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between;margin-left:20px;margin-right:20px}.sectors-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;margin-bottom:30px}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-tooltip{position:absolute;left:80%;width:440px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#DADADA;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:80%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@media screen and (max-width: 1010px){.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center;margin-left:20px;margin-right:20px}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;text-align:center;color:#FFFFFF;margin:42px 20px 30px}.sectors-list{display:flex;flex-direction:column;justify-content:center;align-items:center}.sectors-list-item{width:340px;display:grid;grid-template-columns:auto;height:240px}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-tooltip{position:absolute;left:10%;top:10%;width:310px;height:220px;pointer-events:none;-webkit-appearance:tooltip;-moz-appearance:tooltip;appearance:tooltip;background-color:#DADADA;padding:8px 16px 8px 8px;-webkit-clip-path:polygon(0 0, 100% 0%, 100% 100%, 0% 100%);clip-path:polygon(0 0, 100% 0%, 100% 100%, 0% 100%);z-index:1}}

.footer{border-top:1px solid #FFFFFF}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column;margin-bottom:88px;margin-left:20px}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#fff;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#fff;margin-left:20px}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#fff;margin-bottom:14px;margin-left:20px}.text-yellow{color:#EDE852}

.air-q{padding:50px 0}.air-q-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff}.air-q-map{height:560px;margin:42px 20px;align-content:center}.air-q-text{font-weight:normal;font-size:23px;line-height:27px;color:#fff;margin-left:20px;margin-right:20px}

.hero-mobile{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality-gh-page/static/media/hero-bg.5d86912d.jpg);background-size:cover;height:900px;padding:80px 0 178px}.hero-mobile-partners{display:flex;justify-content:center;width:auto}.hero-mobile-partners-item{margin-left:60px}.hero-mobile-partners-item:first-child{margin-left:0}.hero-mobile-partners-item:last-child{margin-left:100px;margin-right:20px}.hero-mobile-navigation{display:grid;text-align:center;align-items:center;justify-content:center;margin-top:58px;margin-bottom:88px}.hero-mobile-navigation-item{text-decoration:none;font-weight:normal;font-size:19px;line-height:22px;color:#fff;margin-top:20px;margin-bottom:20px}.hero-mobile-title{width:auto;text-align:center;font-weight:normal;font-size:32px;line-height:37px;color:#fff}.hero-mobile-link{display:flex;justify-content:center;width:280px;padding:35px 50px;margin-top:200px;text-decoration:none;font-weight:normal;font-size:28px;line-height:33px;color:#fff;background:#B78A3F}.super-container{display:flex;justify-content:center}

.footer-mobile{border-top:1px solid #FFFFFF}.footer-mobile-partners{display:flex;align-items:center;justify-content:center;width:auto}.footer-mobile-partners-item{margin-right:120px}.footer-mobile-partners-item:last-child{margin-right:0}.footer-mobile-text-wrap{display:flex;justify-content:space-between}.footer-mobile-nav{display:grid;text-align:center;align-items:center;justify-content:center;margin-top:58px;margin-bottom:88px}.footer-mobile-nav-link{font-weight:normal;font-size:15px;line-height:18px;text-align:center;text-decoration:none;color:#fff;margin-bottom:20px}.footer-mobile-nav-link:last-child{margin-bottom:0}.footer-mobile-nav-text{width:auto;margin-top:112px;font-weight:normal;font-size:15px;line-height:18px;color:#fff;text-align:center;margin-bottom:88px;margin-left:20px;margin-right:20px}.footer-mobile-text{display:grid;text-align:center;align-items:center;justify-content:center;font-weight:300;font-size:14px;line-height:14px;color:#fff;margin-bottom:6px;text-align:center}.text-yellow{color:#EDE852}

